import React from 'react';
import Header from 'src/components/Header.jsx';
import { Helmet } from 'react-helmet';
import styles from 'src/styles/commands.module.css';

const config = {
    state: {
        active: '✔️',
        unactive: '❌'
    },
    AvatarURL: 'https://cdn.discordapp.com/avatars/717434146871443456/0a83c747852f4097f0ddba5cc9365760.png'
};

class CommandGen extends React.Component {
    render() {
        return (
            <table className={styles.commands}>
                <thead>
                    <tr>
                        <td>
                            {this.props.h1}
                        </td>
                        <td>
                            {this.props.h2}
                        </td>
                        <td>
                            {this.props.h3}
                        </td>
                        <td>
                            {this.props.h4}
                        </td>
                    </tr>
                </thead>
                <tbody>
                    {this.props.children}
                </tbody>
            </table>
        );
    };
};

class Command extends React.Component {
    render() {
        return (
            <tr>
                <td>
                    {this.props.state}
                </td>
                <td className={styles.c}>
                    <code>
                        {this.props.command}
                    </code>
                </td>
                <td className={styles.c}>
                    <code>
                        {this.props.module}
                    </code>
                </td>
                <td>
                    {this.props.description}
                </td>
            </tr>
        );
    };
};

class Commands extends React.Component {
    render() {
        return (
            <div className={styles.root}>
                <Helmet>
                    <title>
                        Kanami Community - Команды
                    </title>
                    <link rel="shortcut icon" href={config.AvatarURL} type="image/x-icon"/>
                    <meta name="viewport" content="width=device-width, initial-scale=1.0"/>
                </Helmet>
                <Header />
                <CommandGen h1="Состояние" h2="Команда" h3="Модуль" h4="Описание">
                    <Command state={config.state.active} command="?ban" module="Administration" description="Забанить пользователя на сервере." />
                    <Command state={config.state.active} command="?unban" module="Administration" description="Разбан пользователя." />
                    <Command state={config.state.active} command="?kick" module="Administration" description="Изгнать пользователя." />
                    <Command state={config.state.active} command="?addrole" module="Administration" description="Выдать роль пользователю." />
                    <Command state={config.state.active} command="?removerole" module="Administration" description="Отобрать роль у пользоваетля." />
                    <Command state={config.state.active} command="?lockdown" module="Administration" description="Блокировать канал." />
                    <Command state={config.state.active} command="?timedlockdown" module="Administration" description="Блокировка канала на определённое время." />
                    <Command state={config.state.active} command="?unlockdown" module="Administration" description="Разблокировать канал." />
                    <Command state={config.state.active} command="?say" module="Administration" description="Написать в канал." />
                    <Command state={config.state.active} command="?clear" module="Administration" description="Очистка сообщений в чате." />
                    <Command state={config.state.active} command="?botinfo" module="Info" description="Информация о боте." />
                    <Command state={config.state.active} command="?userinfo" module="Info" description="Информация о пользователе." />
                    <Command state={config.state.active} command="?serverinfo" module="Info" description="Информация о сервере." />
                    <Command state={config.state.active} command="?avatar" module="Info" description="Аватар пользователя." />
                    <Command state={config.state.active} command="?changelog" module="Info" description="Информация о обновлениях которые идут каждый день." />
                    <Command state={config.state.active} command="?hug" module="Reactions" description="Обнять пользователя :З" />
                    <Command state={config.state.active} command="?pat" module="Reactions" description="Погладить пользователя :З" />
                    <Command state={config.state.active} command="?kiss" module="Reactions" description="Поцеловать пользователя :З" />
                    <Command state={config.state.active} command="?poke" module="Reactions" description="Тыкать пользователя :З" />
                    <Command state={config.state.active} command="?nom" module="Reactions" description="Покормить пользователя :З" />
                    <Command state={config.state.active} command="?tickle" module="Reactions" description="Щекотать пользоваетеля :З" />
                    <Command state={config.state.active} command="?smug" module="Reactions" description="Смущаться :З" />
                    <Command state={config.state.active} command="?slap" module="Reactions" description="Ударить пользователя :(" />
                    <Command state={config.state.active} command="?cat" module="Reactions" description="Показать картнку котика." />
                    <Command state={config.state.active} command="?dog" module="Reactions" description="Показать картнку собачки." />
                    <Command state={config.state.active} command="?kitsune" module="Reactions" description="Показать картинку лисика." />
                    <Command state={config.state.active} command="?neko" module="Reactions" description="Показать картинку кошко-девочки." />
                    <Command state={config.state.active} command="?nekogif" module="Reactions" description=" Показать анимрованную картинку кошко-девочки." />
                    <Command state={config.state.active} command="?bug" module="Other" description="Отправить сообщение о баге." />
                    <Command state={config.state.active} command="?suggest" module="Other" description="Отправить предложение." />
                    <Command state={config.state.active} command="?8ball" module="Other" description="Шар предскозаний (не верьте ему, он бака!)" />
                </CommandGen>
            </div>
        );
    };
};

export default Commands;